	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Weekend off","date":"02 Sep 2000","slug":"/2000-09-02-weekend-off/","tags":"Y2000"},"html":"<p>I had taken no have no plans for this weekend, as we had planned a web relaunch for Sept 1. But the delivery has been delayed until Monday, and so we have the weekend off. Maybe we'll go the the Expo tomorrow.</p>"},"next":{"fields":{"title":"Welcome to the real world","date":"01 Sep 2000","slug":"/2000-09-01-welcome-to-the-real-world/"}},"previous":{"fields":{"title":"Expo Day","date":"03 Sep 2000","slug":"/2000-09-03-expo-day/"}}},{"node":{"fields":{"title":"Welcome to the real world","date":"01 Sep 2000","slug":"/2000-09-01-welcome-to-the-real-world/","tags":"Y2000"},"html":"<p>A hearty welcome to the world to <a href=\"http://traumwind.editthispage.com/2000/09/01\">Raphael Nicolaj Spernau</a>, the latest editthispage baby! </p>\n<p><b>Cut off!</b> Our flatrate Internet provider has closed all accounts and filed for bankruptcy! Good thing we only had one month left on our prepaid account. I think we'll switch to the <a href=\"http://www.dtag.de\">Deutsche Telecom-edians</a>, er, I mean <a href=\"http://www.t-online.com\">T-Offline</a>. They now offer a flatrate of their own, and they won't be going broke any time soon. And maybe someday they'll offer T-DSL in backwoods places like Lüllau. (And maybe someday will be March 2001....)</p>"},"next":{"fields":{"title":"Christopher's first ride","date":"29 Aug 2000","slug":"/2000-08-29-christophers-first-ride/"}},"previous":{"fields":{"title":"Weekend off","date":"02 Sep 2000","slug":"/2000-09-02-weekend-off/"}}},{"node":{"fields":{"title":"Christopher's first ride","date":"29 Aug 2000","slug":"/2000-08-29-christophers-first-ride/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/08/carousel.jpg\" height=\"400\" width=\"300\" border=\"0\" alt=\"carousel: PapaScott & Christopher are riding the fire truck\" /><br />\n<b><i>Christopher did all right for his first big ride. He was ready to get off after the second round. But he was brave and stayed on without making a fuss.</i></b></p>"},"next":{"fields":{"title":"First Day of School","date":"25 Aug 2000","slug":"/2000-08-25-first-day-of-school/"}},"previous":{"fields":{"title":"Welcome to the real world","date":"01 Sep 2000","slug":"/2000-09-01-welcome-to-the-real-world/"}}}],"pathPrefix":"","first":false,"last":false,"index":1044,"pageCount":1101}}

